const express = require('express');
const controller = require('./inShipment.controller');

import oauth from '../../../components/oauth/auth';

const router = express.Router();

router.get('/:shipmentId/inShipments', oauth, controller.index);
router.get('/:shipmentId/inShipments/:id', oauth, controller.show);
router.post('/:shipmentId/inShipments/', oauth, controller.create);
router.post('/:shipmentId/inShipments/:id', oauth, controller.update);
router.put('/:shipmentId/inShipments/:id', oauth, controller.update);
router.post('/:shipmentId/inShipments/:id', oauth, controller.destroy);

module.exports = router;
